import React, { useForm, useEffect } from 'react';

import { Form, Input, Button, Tabs, message } from 'antd';
import useConnect from 'Src/contextRedux/useConext';
import { storeContext } from 'Src/contextRedux/store';
import { UserOutlined, LockOutlined, WhatsAppOutlined, VerifiedOutlined, MailOutlined } from '@ant-design/icons';
import service from 'Src/utils/request';

import styles from './index.css'

// const TestContext= React.createContext({});

const { TabPane, } = Tabs;
const layout = {
  labelCol: { span: 8, },
  wrapperCol: { span: 16, },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16, },
};

const Login = (props) => {
  const [form] = Form.useForm();
  const { state, dispatch, } = useConnect(storeContext);
  // console.log(8888, state, dispatch,)
  // useEffect(() => {
  //   console.log(222, window.Bmob, props)
  //   // Bmob.User.users().then(res => {
  //   //   console.log(res)
  //   // }).catch(err => {
  //   //   console.log(err)
  //   // })

  //   const current = window.Bmob.User.current()
  //   console.log(111, current)
  // }, [])
  const onLoginFinish = values => {
    console.log('Success:', values);
    window.Bmob.User.login(values.username, values.password).then(res => {
      console.log(res)
      message.success('');
      dispatch({ type: 'change_userName', params: values.username, })
      setTimeout(() => {
        // props.history.goBack()
        props?.history?.goBack()
        // window.history.back()
        // Bmob.User.users().then(res => {
        //   console.log(677777, res)
        // }).catch(err => {
        //   console.log(err)
        // })
      }, 500)
    }).catch(err => {
      console.log(err)
      if (typeof err.error === 'string') {
        message.warning(err.error.slice(0, 300))
      } else {
        message.error('')
      }
    });
  };

  const onFinishFailed = errorInfo => {
    console.log('Failed:', errorInfo);
  };
  const onRegisterFinish = (values) => {
    console.log(66, values);
    window.Bmob.User.register(values).then(res => {
      message.success('');
    }).catch(err => {
      if (typeof err.error === 'string') {
        message.warning(err.error.slice(0, 300))
      } else {
        message.error('')
      }
      console.warn(44, err)
    });
  };
  const getsmsCodeFn = () => {
    const phonee = form.getFieldValue('phone')
    console.log('444:', phonee);
  };
  return (
    <div className={styles.login} >
      <Tabs
        defaultActiveKey='login'
      // onChange={callback}
      >
        <TabPane tab='' key='login'>
          <Form
            className={styles.form_wrap}
            form={form}
            name='login'
            // initialValues={{ remember: true, }}
            onFinish={onLoginFinish}
            // onFinishFailed={onFinishFailed}
          >
            <Form.Item
              // label=''
              name='username'
              rules={[{ required: true, message: '', }]}
            >
              <Input
                placeholder=''
                prefix={<UserOutlined className='site-form-item-icon' />}
              />
            </Form.Item>

            <Form.Item
              // label=''
              name='password'
              rules={[{ required: true, message: '', }]}
            >
              <Input.Password
                placeholder=''
                prefix={<LockOutlined className='site-form-item-icon' />}
              />
            </Form.Item>

            <Form.Item >
              <Button type='primary' htmlType='submit'></Button>
              {/* <Button className={styles.reset_btn} onClick={onReset}></Button> */}
            </Form.Item>
          </Form>

        </TabPane>

        <TabPane tab='' key='register'>
          <Form
            className={styles.form_wrap}
            form={form}
            name='register'
            // initialValues={{ remember: true, }}
            onFinish={onRegisterFinish}
            // onFinishFailed={onFinishFailed}
          >
            <Form.Item
              // label=''
              name='username'
              rules={[{ required: true, message: '', }]}
            >
              <Input
                placeholder=''
                prefix={<UserOutlined className='site-form-item-icon' />}
              />
            </Form.Item>
            <Form.Item
              // label=''
              name='email'
              rules={[
                {
                  type: 'email',
                  message: '',
                },
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              <Input
                placeholder=''
                prefix={<MailOutlined className='site-form-item-icon' />}
              />
            </Form.Item>
            <Form.Item
              // label=''
              name='phone'
              rules={[
                {
                  pattern: /^1[3456789]\d{9}$/,
                  message: '',
                },
                {
                  required: false,
                  message: '',
                },
              ]}
            >
              <Input
                placeholder=''
                prefix={<WhatsAppOutlined className='site-form-item-icon' />} />
            </Form.Item>
            {/* <Form.Item
              // label=''
              name='smsCode'
              rules={[{ required: true, message: '', }]}
            >
              <Input.Search
                enterButton=''
                placeholder=''
                onSearch={getsmsCodeFn}
                prefix={<VerifiedOutlined className='site-form-item-icon' />}
              />
            </Form.Item> */}
            <Form.Item
              // label=''
              name='password'
              rules={[{ required: true, message: '', }]}
            >
              <Input.Password
                placeholder=''
                prefix={<LockOutlined className='site-form-item-icon' />}
              />
            </Form.Item>

            <Form.Item >
              <Button type='primary' htmlType='submit'></Button>
            </Form.Item>
          </Form>

        </TabPane>
      </Tabs>

    </div>
  )
}
export default Login
